.App {
  font-family: sans-serif;
  text-align: center;
}
.action-items {
  position: fixed;
  bottom: 0;
}
.screenplay {
  width: 90%;
  height: auto;
}

.action-items-mobile {
  /* Add mobile-specific styles here */
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
}

.action-items-mobile.minimized {
  /* Add styles for minimized container */
}

.mobile-btn {
  /* Add mobile-specific styles here */
  height: 100%;
  text-align: center;
  font-size: 14px;
  border-radius: 5px;
  border: none;
  background-color: gray;
  color: white;
  margin: 2px;
  flex: 1;
}
.mobile-btn:active {
  /* Add "active" state styles here */
  transform: translateY(2px);
}
.text-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.message-input {
  padding: 10px;
  border-radius: 20px;
  border: none;
  background-color: #f2f2f2;
  font-size: 16px;
  margin-right: 10px;
  width: 70%;
}

.start-button.start {
  padding: 10px 20px;
  border-radius: 50%;
  background-color: #075e54;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
}
.start-button.stop {
  padding: 10px 20px;
  border-radius: 50%;
  background-color: red;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
}
.file-options {
  display: flex;
  flex-direction: row;
}
